import React, { useEffect, useState } from "react";
import { Button } from "react-bootstrap";
import { BsTrashFill, BiEdit, FcTodoList } from "react-icons/all";

function Todo() {
  // const [show, setShow] .{}= useState(false);
  const getLocalitems = () => {
    let list = localStorage.getItem("Todo");
    if (list) {
      return JSON.parse(localStorage.getItem("Todo"));
    } else {
      return [];
    }
  };
  const [todo, setTodo] = useState(getLocalitems());
  const [inputData, setInputData] = useState("");
  const [data, setData] = useState(true);
  const [dta, setDta] = useState(null);
  // function Example() {
  //     <>
  //       <Modal show={show}>
  //         <Modal.Header closeButton>
  //           <Modal.Title>Modal heading</Modal.Title>
  //         </Modal.Header>
  //         <Modal.Body>Woohoo, you're reading this text in a modal!</Modal.Body>
  //         <Modal.Footer>
  //           <Button variant="secondary" onClick={setShow(false)}>Close</Button>
  //         </Modal.Footer>
  //       </Modal>
  //     </>
  // }
  const additem = (e) => {
    if (!inputData) {
      window.alert("Please enter what to do");
    } else if (inputData && !data) {
      setTodo(
        todo.map((item) => {
          if (item.id === dta) {
            return { ...item, name: inputData };
          }
          return item;
        })
      );
      setInputData("");
      setData(true);
      setDta(null);
    } else {
      const allInputData = {
        id: new Date().getTime().toString(),
        name: inputData,
      };
      setTodo([...todo, allInputData]);
      console.log(todo);
    }
  };
  useEffect(() => {
    localStorage.setItem("Todo", JSON.stringify(todo));
  }, [todo]);
  const edit = (id) => {
    let newEdit = todo.find((item) => {
      return item.id === id;
    });
    setData(false);
    setInputData(newEdit.name);
    setDta(id);
  };
  const delee = (id) => {
    const updatesitems = todo.filter((item) => {
      return id !== item.id;
    });
    setTodo(updatesitems);
  };
  return (
    <div className="todo container-fluid">
      <div className="py-5 container-fluid">
        <div className="container div1 rounded-3 p-1">
          <h1 className="h1 text-center text-white-50 p-3 rounded-3">
            <FcTodoList />
            Todo App
          </h1>
          <div className="div2 d-flex justify-content-between mx-4 my-2">
            <input
              type="text"
              placeholder="Enter what to do..."
              className="input1 rounded-3 bg-transparent text-white-50 w-50"
              value={inputData}
              // onSubmit={() => additem()}
              onChange={(e) => setInputData(e.target.value)}
            />
            <div className="d-flex justify-content-between btn1">
              {data ? (
                <Button
                  className="rounded-3 me-2 px-2"
                  onClick={() => additem()}
                >
                  Add
                </Button>
              ) : (
                <Button
                  className="rounded-3 me-2 px-2"
                  onClick={() => additem()}
                >
                  Edit
                </Button>
              )}
              <Button
                className="rounded-3 px-2"
                onClick={() => {
                  setTodo([]);
                }}
              >
                Delete All
              </Button>
            </div>
          </div>
        </div>
        <div className="container text-left p-0">
          {todo.map((item, ind) => {
            return (
              <div className="div1 mt-5 px-4 rounded d-flex justify-content-between">
                <h3
                  className="mb-1 p-1 "
                  key={item.id}
                >
                  <b className="position-absolute bold">
                    {ind}, {item.name}
                  </b>
                  <div className=" position-relative right">
                    <BiEdit
                      className=" text-left flex-0 "
                      onClick={() => edit(item.id)}
                    />
                    <BsTrashFill
                      className=" text-left  "
                      onClick={() => delee(item.id)}
                    />
                  </div>
                </h3>
              </div>
            );
          })}
        </div>
      </div>
    </div>
  );
}

export default Todo;
